// ""   
class OSIApp {
    constructor(data = {}) {
        this.id = data.id || ('newApp_' + Date.now());
        this.name = data.name || 'NewApp';
        this.label = data.label || ' ';
        this.category = data.category || 'other';
        this.icon = data.icon || '';                            // - (  ; TODO:     )
        this.iconclass = data.iconclass || 'text-ico-tech';     //   bootstrap   
        this.description = data.description || '';              //  
        this.defWidth = data.defWidth || 800;                   //  -
        this.defHeight = data.defHeight || 450;                 //  -
        this.isMaximized = data.isMaximized || false;           //    
        this.resizable = data.resizable || false;               //    
        this.canMinimize = data.canMinimize || false;           //   
        this.showOnDesktop = data.showOnDesktop || false;       //    
        this.showInStartMenu = data.showInStartMenu || false;   //    ""
    }
}

